<template>
  <div>
    <h1>登入後台</h1>
    <div class="container">
      <form class="form-signin" @submit.prevent="signin">
        <h1 class="h3 mb-3 font-weight-normal">
          登入系統
        </h1>
        <div class="form-group">
          <label for="inputEmail" class="sr-only">Email address</label>
          <input id="inputEmail" v-model="user.email" type="email" class="form-control"
          placeholder="Email address"
            required autofocus>
        </div>
        <div class="form-group">
          <label for="inputPassword" class="sr-only">Password</label>
          <input id="inputPassword" v-model="user.password" type="password" class="form-control"
            placeholder="Password" required>
        </div>
        <button class="btn btn-lg btn-info btn-block" type="submit">
          登入
        </button>
        <p class="mt-5 mb-3 text-muted">
          &copy; 線上交易平台
        </p>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        email: '',
        password: '',
      },
    };
  },
  methods: {
    signin() {
      const api = `${process.env.VUE_APP_APIPATH}api/auth/login`;
      this.$http.post(api, this.user)
        .then((res) => {
          const { token } = res.data;
          const { expired } = res.data;
          document.cookie = `token=${token};expires=${new Date(expired * 1000)};path=/`;
          window.location = 'http://localhost:8080/#/admin/products';
          console.log(res, token, expired);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
